export const calculationResultTableHeaders = [
  {
    title: "População",
    field: 'populationName',
  },
  {
    title: "Numero de amostras",
    field: 'samples',
  },
  {
    title: "FA1",
    field: 'FA1',
  },
  {
    title: "FA3",
    field: 'FA3',
  },
]

export const deserializeTableValues = data =>
  data.map(row => ({
    ...row,
    samples: row.samples.length,
    FA1: row.FA1.toFixed(2),
    FA3: row.FA3.toFixed(4),
  }))
