import React from 'react';
import { Input } from '@material-ui/core';
import { v4 as getUUID } from 'uuid';

const validations = {
  required: value => value != null && value !== '',
  min: (value, min) => Number(min) <= Number(value)
}

export const sampleIsValid = ({ populationName, leftSize, rightSize }) => (
  validations.required(populationName) &&
  validations.required(leftSize) &&
  validations.required(rightSize) &&
  validations.min(leftSize, 0.001) &&
  validations.min(rightSize, 0.001)
)

export const samplesTableColumns = [
  {
    title: "População",
    field: 'populationName',
    validate: ({ populationName }) => validations.required(populationName)
  },
  {
    title: "Tamanho Direita",
    field: 'rightSize',
    validate: ({ rightSize }) => validations.required(rightSize),
    align: 'left',
    editComponent: ({ value, onChange, error }) =>
      <Input
        required
        style={{ fontSize: "13px" }}
        placeholder="Tamanho Direita"
        min="0"
        value={value || ''}
        onChange={({ target }) => onChange(target.value)}
        error={error}
        type="number"
      />,
    cellStyle: { textAlign: 'left' }
  },
  {
    title: "Tamanho Esquerda",
    field: 'leftSize',
    validate: ({ leftSize }) => validations.required(leftSize),
    align: 'left',
    editComponent: ({ value, onChange, error }) =>
      <Input
        required
        style={{ fontSize: "13px" }}
        placeholder="Tamanho Esquerda"
        min="0"
        value={value || ''}
        onChange={({ target }) => onChange(target.value)}
        error={error}
        type="number"
      />,
    cellStyle: { textAlign: 'left' }
  },
]

export const generateNewSample = ({ leftSize, rightSize, ...sample }) => ({
  ...sample,
  leftSize: Number(leftSize),
  rightSize: Number(rightSize),
  id: getUUID()
})
