const calculate = samples => {
  const groupedPopulations = Utils.groupBy(samples, 'populationName');

  const populationsResult = fluctuatingAsymmetry(groupedPopulations)

  return populationsResult;
}

const getAverage =
  callback =>
    items =>
      items.reduce((total, item) => total + callback(item), 0) / items.length

const calculateFA1 = getAverage(sample => Math.abs(sample.rightSize - sample.leftSize))
const calculatePopulationSizeAverage = getAverage(sample => (sample.rightSize + sample.leftSize) / 2)

const fluctuatingAsymmetry = (population) => {
  return Object.keys(population).reduce((results, individual) => {
    const FA1 = calculateFA1(population[individual].samples)
    const populationSizeAverage = calculatePopulationSizeAverage(population[individual].samples)

    return [
      ...results,
      {
        populationName: individual,
        samples: population[individual].samples,
        FA1,
        FA3: FA1 / populationSizeAverage
      }
    ]
  }, [])
}

const Utils = {
  groupBy: (array, attribute) =>
    array.reduce((groups, item) => {
      const currentElements = groups[item[attribute]] || { samples: [] }

      groups[item[attribute]] = { samples: [...currentElements.samples, item] }

      return groups
    }, {}),
}

export default calculate;
